@charset "UTF-8";
html {
  font-size: 16px;
}

body {
  font-family: "Source Sans Pro", Arial, sans-serif;
  font-size: 1rem;
}

body:before {
  background-color: #fcf8e3;
  border-bottom: 1px solid #fbeed5;
  border-left: 1px solid #fbeed5;
  color: #c09853;
  font: small-caption;
  padding: 3px 6px;
  pointer-events: none;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 100;
}
@media (min-width: 0em) {
  body:before {
    content: "small ≥ 0px (0em)";
  }
}
@media (min-width: 40em) {
  body:before {
    content: "medium ≥ 640px (40em)";
  }
}
@media (min-width: 48em) {
  body:before {
    content: "large ≥ 768px (48em)";
  }
}
@media (min-width: 80em) {
  body:before {
    content: "wide ≥ 1280px (80em)";
  }
}

body.artist .artist__header.flex {
  display: flex;
  flex-direction: column;
}
body.artist .image-portrait {
  padding-bottom: 100vh;
}
body.artist .image-portrait img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.artist__type {
  font-family: "Source Sans Pro", Arial, sans-serif;
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 0.03rem;
  font-weight: 700;
}

h1.name {
  font-family: "Cormorant Garamond", Georgia, serif;
  font-size: calc(1.545rem + 3.54vw);
  padding: 0;
  margin: 0;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.05rem;
  line-height: 0.8;
}
@media (min-width: 1200px) {
  h1.name {
    font-size: 4.2rem;
  }
}

@media (min-width: 48em) {
  body.artist .artist__header.flex {
    display: flex;
    flex-direction: row;
  }
  body.artist .image-portrait {
    padding-bottom: 100vh;
    width: 50vw;
  }
  body.artist .image-portrait img {
    width: 50vw;
    height: 100vh;
    object-fit: cover;
  }
  body.artist .artist__title {
    color: blue;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    background: black;
    width: 50vw;
    border: 1px solid red;
  }
}
.image-landscape,
.image-portrait {
  display: block;
  position: relative;
  height: 0;
  overflow: hidden;
}
.image-landscape img,
.image-portrait img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  max-width: inherit;
}

.image-landscape {
  padding-bottom: calc((9 / 16) * 1000px);
}

body {
  margin: 0;
  padding: 0;
}

@media (min-width: 48em) {
  body {
    padding: 0 3%;
  }
}
@font-face {
  font-family: "swiper-icons";
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}
:root {
  --swiper-theme-color: #007aff;
}

.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-container-autoheight, .swiper-container-autoheight .swiper-slide {
  height: auto;
}
.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */
.swiper-container-3d {
  perspective: 1200px;
}
.swiper-container-3d .swiper-wrapper, .swiper-container-3d .swiper-slide, .swiper-container-3d .swiper-slide-shadow-left, .swiper-container-3d .swiper-slide-shadow-right, .swiper-container-3d .swiper-slide-shadow-top, .swiper-container-3d .swiper-slide-shadow-bottom, .swiper-container-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}
.swiper-container-3d .swiper-slide-shadow-left, .swiper-container-3d .swiper-slide-shadow-right, .swiper-container-3d .swiper-slide-shadow-top, .swiper-container-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}
.swiper-container-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-container-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-container-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}
.swiper-container-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}
.swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}
.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

img {
  width: 50%;
}

.swiper-container {
  width: 50%;
  height: 50%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}